<template>
  <component :is="as" :class="textVariants({ weight, size })">
    <slot />
  </component>
</template>

<script setup lang="ts">
import { textVariants } from '.';
import { computed } from 'vue';

type Props = {
  size?: NonNullable<Parameters<typeof textVariants>[0]>['size'];
  weight?: NonNullable<Parameters<typeof textVariants>[0]>['weight'];
  color?: string;
  as?: string;
};

const props = withDefaults(defineProps<Props>(), {
  as: 'span',
  color: 'text-primary',
});

const color = computed(() => `var(--${props.color})`);
</script>

<style>
.global-text {
  color: v-bind('color') !important;
}
</style>
